.FaqTitle {
    display: flex;
    padding: 20px;
    background: #F9F9F9;
    justify-content: space-between;
    flex-direction: row;
    align-items: center;
    line-height: normal;
    cursor: pointer;
}
.FaqTitle > span {
    width: 20px;
}
.FaqTitle > p {
    margin: 0;
}
.FaqBody {
    padding: 20px;
    background: #F9F9F9;
    margin-top: 5px;
    margin-bottom: 20px;
    transition: all .4s ease;
    overflow: hidden;
}
.FaqHeader {
    margin-bottom: 50px;
}