.Events {
    margin-top: 100px;
    padding: 0 10%;
}
.EventItems {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-start;
}
.EventItem {
    width: 23%;
    margin: 0 1%;
    margin-top: 30px;
}
@media screen and (max-width: 800px) {
    .Events {
        padding: 0 2%;
    }
    /* .EventItem {
        width: 25%;
    } */
}
@media screen and (max-width: 500px) {
    .EventItems {
        flex-direction: column;
        padding: 5%;
    }
    .EventItem {
        width: 100%;
        margin: 0;
        margin-bottom: 30px;
    }
}