.OrderSummaryItem {
    border: 1px solid #c6c6c6;
    padding: 20px;
    border-radius: 5px;
    margin: 30px 0;
}
.OrderSummaryItem > img {
    width: 100%;
    margin-bottom: 20px;
}
.OrderSummaryKeyValue {
    display: flex;
    margin: 10px 0;
}
.OrderSummaryKeyValue > div {
    width: 50%
}
.OrderSummaryKey {
    font-weight: bold;
}
.PrevNext {
    display: flex;
    justify-content: space-between;
}
.PrevNext > div {
    padding: 10px 30px;
    background: #FFC657;
    border-radius: 5px;
}
@media screen and (max-width: 500px) {
    .CheckoutContainer {
        flex-direction: column-reverse;
    }
    .CheckoutContainer > div {
        width: 100%;
    }
    .InputContainer > div {
        width: 50%;
    }
    .CheckoutLeft {
        margin-top: 70px;
    }
}