.OtherHeader {
    display: flex;
    background-color: #212224;
    height: 300px;
    padding: 20px 10%;
    color:  #FFC657;
}
.OtherHeader > div {
    width: 50%;
}
.OtherHeaderLeft {
    background-image: url('../../assets/images/header-right-backg.png');
    background-repeat: no-repeat;
    background-size: contain;
    transform: rotateY(180deg);
    background-position: right;
    height: 100%;
}
.OtherHeaderLeft > div {
    transform: rotateY(180deg);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-start;
    height: 100%;
}
.OtherHeaderRight {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: flex-end;
}
.Cart, .OtherHeaderRight > a {
    width: 154px;
    height: 48px;
    left: 1139px;
    top: 47px;
    color: black;
    background: #FFC657;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.OtherHeaderRight > a:hover, .OtherHeaderRight > a:active {
    color: black;
}
.Cart {
    color: black;
    display: flex;
    width: fit-content;
    padding: 5px 5px 5px 10px;
    align-items: center;
}
.CartIcon > i {
    font-size: 2em;
}
.Counter{
    background: white;
    border-radius: 50%;
    font-size: .8em;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    right: 20px;
    top: -10px;
    width: 15px;
    height: 15px;
}
.CartContainer {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
}
.CartContainer > span {
    color: white;
    font-weight: bold;
}
a {
    color: black;
}
@media screen and (max-width: 500px) {
    .OtherHeader {
        height: 60px;
        padding: 0 5%;
        flex-direction: column;
    }
    .OtherHeader > div {
        width: 100%;
    }
    .OtherHeaderLeft {
        background: none;
    }
    .OtherHeaderRight {
        background: white;
        flex-direction: row;
        justify-content: space-between;
        margin: 20px 0;
    }
    .OtherHeaderRight > a {
        background: none;
        font-size: .6em;
        padding: 5px 15px;
        border: 1px solid black;
        width: fit-content;
        height: fit-content;
    }
    .CartIcon {
        font-size: .5em;
        padding: 0;
    }
    .OtherHeaderRight{
        margin-top: 20px;
    }
    .OtherHeaderLeft > img {
        margin-top: 10px;
    }
    .OtherHeaderLeft > div > h2 {
        display: none;
    }
}