* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Roboto';
}
.Container {
  padding: 100px 10%;
  text-align: justify;
}
.CartItemContainer {
  margin: 30px 0;
  padding: 30px 0;
}
.CartItems {
  box-shadow: 4px 4px 10px 5px #c4c4c48c;
  padding: 30px;
}
.CartFlex {
  display: flex;
  padding: 10px;
}
.CartCard {
  box-shadow: 4px 4px 10px 5px #c4c4c48c;
}
.CartFlex > * {
  padding: 0 2%;
}
.CartTitle {
  font-weight: bold;
}
.CartItem {
  width: 50%;
}
.CartUnitPrice, .CartQuantity, .CartSubTotal {
  width: calc(50% / 3);
}
.CartItemFlex {
  display: flex;
  justify-content: space-between;
}
.CartItemFlex > img {
  width: 30%;
}
.CartItemFlex > p {
  width: 65%;
}
.CartQuantity > input {
  width: 50%;
}
.Remove {
  display: flex;
  justify-content: flex-end;
  color: #FF4600;
  align-items: center;
  margin-right: 30px;
}
.Remove > div {
  cursor: pointer;
}
.Remove > div > i {
  margin-right: 20px;
}
.Shop {
  box-shadow: 4px 4px 10px 5px #d3d3d394;
  padding: 50px 20px;
}
.Product {
  display: flex;
  margin: 20px 0;
  padding: 20px;
  transition: .4s ease all;
  font-size: bold;
  justify-content: space-between;
}
.Product:hover {
  box-shadow: 4px 4px 10px 5px #d3d3d394;
}
.ProductContent {
  display: flex;
  padding: 20px;
  justify-content: space-between;
  width: 70%;
}
.Product > img {
  width: 23%;
  height: 250px;
}
.ProductContent {
  width: 70%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.PriceAddToCart {
  display: flex;
  justify-content: space-between;
}
.ProductButton {
  display: none;
}
.Product .ProductButton {
  display: block;
  width: 154px;
  height: 48px;
  background: #FFC657;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.CartButtons {
  display: flex;
  justify-content: flex-end;
  margin-top: 100px;
}
.BackToShop {
  border: 2.64286px solid #000000;
  padding: 10px 30px;
  margin-right: 20px;
}
.BackToShop > span {
  margin-left: 20px;
}
.CheckoutButton {
  padding: 10px 30px;
  background-color: #FFC657;
  color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 5px;
}
a, a:hover, a:active {
  color: inherit;
}
.CheckoutContainer {
  box-shadow: 4px 4px 10px 5px #d3d3d394;
  padding: 30px;
  display: flex;
  justify-content: space-between;
}
.CheckoutLeft {
  width: 60%;
  color: #666666;
}
.CheckoutLeft > p {
  margin: 30px 0;
}
.CheckoutRight {
  width: 35%;
}
.InputContainer {
  display: flex;
  justify-content: space-between;
}
.InputContainer > * {
  padding: 0 10px;
}
.Input {
  width: 100%;
  display: flex;
  flex-direction: column;
  margin: 20px 0;
}
.Input > input {
  padding: 10px 20px;
  border: 2px solid black;
  border-radius: 5px;
}
.Input > label > span {
  color: red;
}
.Proceed {
  display: flex;
  justify-content: center;
  margin: 70px 0;
}
.CheckoutRight {
  padding-top: 50px;
}
@media screen and (max-width: 800px) {
  .Container {
    padding: 20px 5%;
  }
  .Product > img {
    height: 150px;
  }
  .Product {
    padding: 5px;
  }
}
@media screen and (max-width: 500px) {
  .Container {
    margin-top: 70px;
  }
  .Shop {
    padding: 5px;
    box-shadow: none;
  }
  .Shop > hr {
    display: none;
  }
  .Product {
    padding: 10px 5%;
    align-items: flex-start;
    box-shadow: 2px 2px 5px 2.5px #d3d3d394;
  }
  .Product:hover {
    box-shadow: none;
  }
  .ProductContent > p, .PriceAddToCart > h2, .ProductButton {
    font-size: .5em;
  }
  .Product > img {
    height: 70px;
  }
  .ProductButton {
    display: block;
    background: #FFC657;
    border-radius: 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    padding: 5px;
  }
  .ProductContent {
    padding: 0;
    /* flex: 1; */
  }
  .CartItems {
    padding: 0;
    box-shadow: none;
  }
  .CartFlex {
    font-size: .5em;
  }
  .CartItem > img {
    max-height: 50px;
  }
  .BackToShop, .CheckoutButton {
    font-size: .6em;
  }
}